* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-info {
  margin-left: 15px;
}
.wrapper {
  display: flex;
  align-items: center;
}

.country-info__name {
  margin-left: 10px;
}

.country-info__features {
  padding: 0px;
}
.country-info__feature {
  font-weight: 600;
}

.country-info__value {
  font-weight: 400;
}

.list {
  list-style: none;
}
